<template> 
  <v-layout row wrap>
    <v-flex>
      <vue-dropzone 
        ref="dropzone" 
        id="dropzone"
        @vdropzone-sending="sendingEvent"
        :options="dropOptions"
        @vdropzone-complete="afterComplete"
      ></vue-dropzone>
    </v-flex>
    <v-flex>
      <v-btn @click="removeAllFiles">{{ $t('global.remove_files_title') }}</v-btn>
    </v-flex>  
  </v-layout>
