        thumbnailHeight: 70,
        headers: { 'USER_NAME': this.$store.state.AppStore.currentUser.user_name },
        addRemoveLinks: true,
        accept (file, done) {
          console.log(file)
          done()
        }
      }
    }
  },
  created () {
    console.log('created')
  },
  methods: {
    afterComplete (file) {
    },
    sendingEvent (file, xhr, formData) {
    },
    removeAllFiles () {
      this.$refs.dropzone.removeAllFiles()
    }
  },
  components: {
    vueDropzone
  }
}
</script>
